<template>
    <div class="main-page">
        <div class="main-content">
            <components :is="pageName" />
        </div>
        <van-tabbar v-model="active">
            <van-tabbar-item :icon="item.icon" v-for="(item, index) in tabbars" :key="index" @click="toPage(item)">{{ item.name }}</van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
export default {
    name: "Home",
    props: {},
    components: {
        Notice: () => import("./notice/Notice.vue"),
        My: () => import("./my/My.vue"),
        Home1: () => import("./dynamicHome/Home1.vue"),
    },
    data() {
        return {
            active: 0,
            pageName: "Notice",
            tabbars: [
                { name: "消息", icon: "comment-o", page: "Notice" },
                { name: "首页", icon: "wap-home-o", page: "Home1" },
                { name: "我的", icon: "contact", page: "My" },
            ]
        }
    },
    methods: {
        toPage(item) {
            this.pageName = item.page
            this.$u.setItem("curTabbarIndex", this.active)
        },

        recoverTabbar(activeIndex) {
            this.active = activeIndex ? activeIndex : Number(localStorage.getItem("curTabbarIndex"))
            this.pageName = this.tabbars[this.active]?.page
        },
    },
    mounted() {
        this.recoverTabbar(1)
    },
    created(){
        this._api.fileQueryList().then(res=>{
            console.log(res)
        })
     
       

    }
}
</script>

<style lang="scss" scoped>
.main-page {
    .main-content {
        height: calc(100% - 50px);
        overflow: auto;
    }

    .router_view {
        width: 100%;
        height: 100%;
    }
}
</style>